.header {
  background-color: #fff;
  padding: 1.75em 1em 1em;
  color: #222;
  max-width: 525px;
  margin: 0 auto 2.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.header--group {
  display: -ms-flexbox;
  display: flex;
  padding-top: 1em;
  padding-right: 1em;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.header--title {
  font-size: 1.5em;
  text-align: left;
  font-weight: normal;
}
.header--intro {
  font-size: 1em;
}
.header--apps {
  width: 100%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
}



